  font-family: 'sell-icon';
  src:  url(data:application/vnd.ms-fontobject;base64,);
  src:  url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'),
    url(data:application/x-font-ttf;base64,) format('truetype'),
    url(data:application/font-woff;base64,) format('woff'),
    url(data:image/svg+xml;base64,#sell-icon) format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'sell-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-add_circle:before {
  content: "\E900";
}
.icon-arrow_lift:before {
  content: "\E901";
}
.icon-check_circle:before {
  content: "\E902";
}
.icon-close:before {
  content: "\E903";
}
.icon-favorite:before {
  content: "\E904";
}
.icon-keyboard_arrow_right:before {
  content: "\E905";
}
.icon-remove_circle_outline:before {
  content: "\E906";
}
.icon-shopping_cart:before {
  content: "\E907";
}
.icon-thumb_down:before {
  content: "\E908";
}
.icon-thumb_up:before {
  content: "\E909";
}
.header{
	height: 130px;
	padding-top: 20px;
}
.header .top-wrapper{
	position: relative;
}
.header .top-wrapper .back-wrapper{
	width: 50px;
	height:31px;
	position: absolute;
	left: 0;
	top: 0;
	text-align: center;
	line-height: 31px;
}
.header .top-wrapper .back-wrapper span{
	display: inline-block;
	color: white;
}
.header .top-wrapper .search-wrapper{
	width: 100%;
	height:31px;
	padding: 0 104px 0 50px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.header .top-wrapper .search-wrapper .search-icon{
    width: 28px;
    height: 31px;
	background: url(data:image/png;base64,) no-repeat 11px;
	background-size: 13px 13px;
	position: absolute;
}
.header .top-wrapper .search-wrapper .search-bar{
	width: 100%;
	height: 31px;
	border: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	background-color: #cdcdcc;
	border-radius: 25px;
	padding-left: 28px;
	/*去除選種顏色*/
	outline: none;
}
.header .top-wrapper .more-wrapper{
	/*width: 104px;*/
	width: 65px;
	height: 24px;
	/*height:31px;*/
	position: absolute;
	right: 0;
	top: 0;
	padding: 7px 15px 0 24px;
}
.header .top-wrapper .more-wrapper .spelling-bt{
	width: 30px;
	height: 17px;
	color: white;
	line-height: 17px;
	border:1px solid white;
	text-align: center;
    float: left;
    text-decoration: none;
    font-size: 10px;
}
.header .top-wrapper .more-wrapper .more-bt{
	float: left;
	width: 20px;
	height: 24px;
	margin-left: 13px;
	margin-top: 6px;
}
.header .top-wrapper .more-wrapper .more-bt .s-radius{
	width: 3px;
	height: 3px;
	border-radius: 50%;
	border:1px solid white;
	display: block;
	float: left;
	margin-right: 1px;
}

/*主體內容*/
.content-wrapper{
	padding: 17px 10px 11px;
	height: 50px;
}
.content-wrapper .icon{
	width: 50px;
	height: 50px;
	background-size: 135% 100%;
	background-position: center;
	border-radius: 5px;
	float: left;
}
.content-wrapper .name{
	float: left;
	padding: 18px 0 0 12px;
}
.content-wrapper .name h3{
	font-size: 16px;
	font-weight: bold;
	color:white;
}
.content-wrapper .collect{
	width: 25px;
	height: 37px;
	float: right;
	text-align: center;
	padding-top: 6px;
}
.content-wrapper .collect img{
	width: 20px;
	height: 20px;
}
.content-wrapper .collect span{
	margin-top: 7px;
	color: white;
	font-size: 11px;
}

/*公告內容*/
.bulletin-wrapper{
	height: 16px;
	padding: 0 10px;
}
.bulletin-wrapper .icon{
	width: 16px;
	height: 16px;
	float: left;
	margin-left: 6px;
}
.bulletin-wrapper .text{
	font-size: 11px;
	color: white;
	float: left;
	line-height: 16px;
}
.bulletin-wrapper .detail{
	color: white;
	float: right;
	font-size: 11px;
	line-height: 16px;
}
.bulletin-wrapper .detail span{
	font-size: 16px;
	line-height: 16px;
	float: right;
}

/*背景*/
.header .bg-wrapper{
	width: 100%;
	height: 150px;
	position: absolute;
	left: 0;
	top:0;
	z-index: -1;

	background-size: 100% 135%;
	background-position: center -12px;
}

/*公告詳情*/
.header .bulletin-detail{
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0;
	top: 0;
	background: rgba(98,98,98,0.8);
	z-index: 2;
}
.header .bulletin-detail .detail-wrapper{
	width: 100%;
	height: 100%;
	padding: 43px 20px 125px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.header .bulletin-detail .detail-wrapper .main-wrapper{
	width: 100%;
	height: 100%;
	background-size: 100% 100%;
	border-radius: 10px;
	text-align: center;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .icon{
	width: 60px;
	height: 60px;
	background-size: 135% 100%;
	background-position: center;
	border-radius: 5px;
    display: inline-block;
    margin-top: 40px;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .name{
	font-size: 15px;
	color: white;
    margin-top: 13px;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .score{
	height: 10px;
	margin-bottom: 6px;
	text-align: center;
	font-size: 0;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .score .star{
	display: inline-block;
	margin-right: 7px;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .score span{
	display: inline-block;
	font-size: 10px;
	color: white;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .tip{
	font-size: 11px;
	color: #bababc;
	margin-top:8px;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .tip i{
	margin: 0 7px;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .time{
	font-size: 11px;
	color: #bababc;
	margin-top:13px;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .discounts{
	margin-top: 20px;
	padding: 0 20px;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .discounts p{
	padding-top: 20px;
	border-top: 1px solid #bababc;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .discounts img{
	width: 16px;
	height: 16px;
	vertical-align: middle;
}
.header .bulletin-detail .detail-wrapper .main-wrapper .discounts span{
	font-size:11px;
	line-height: 16px;
	color: white;
}
.header .bulletin-detail .detail-wrapper .close-wrapper{
	padding-top: 20px;	
	height: 40px;
	text-align: center;
}
.header .bulletin-detail .detail-wrapper .close-wrapper span{
	width: 40px;
	height: 40px;
	line-height: 40px;
	border-radius: 50%;
	font-size: 14px;
	color: white;
	display: inline-block;
	background: rgba(118,118,118,0.7);
	border: 1px solid rgba(140,140,140,0.9);
}

/*vue過渡效果*/
.detail-enter-active,.detail-leave-active{
	-webkit-transition:  2s all;
	transition:  2s all;
}
.detail-enter,.detail-leave-to{
	opacity: 0;
}
.detail-enter-to,.detail-leave{
	opacity: 1;
}

.star{
	font-size: 0;
}
.star .star-item{
	display: inline-block;
	width: 10px;
	height: 10px;
	margin-right: 3px;
	background-repeat: no-repeat;
	background-size: 10px 10px;
}
.star.star-item:last-child{
	margin-right: 0;
}
.star .on {
	background-image: url(data:image/png;base64,);
}
.star .half {
	background-image: url(data:image/png;base64,);
}
.star .off {
	background-image: url(data:image/png;base64,);
}

.nav{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width: 100%;
	height: 40px;
	line-height: 40px;
	border-bottom: 1px solid #e4e4e4;
}
.nav .nav-item{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	text-align: center;
	font-size: 13px;
	text-decoration: none;
	color: #666666;
	position: relative;
}
.nav .active{
	color: #FFBB22
}
.nav .active .line{
	width: 21px;
	height: 2px;
	display: inline-block;
	background:#FFBB22;
	position: absolute;
	left:50%;
	bottom: 0;
	margin-left: -10.5px;
}

.goods{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	position: absolute;
	top: 190px;
	bottom: 51px;

	overflow: hidden;
	width: 100%;
}
.goods .menu-wrapper{
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 85px;
	        flex: 0 0 85px;
	background: #f4f4f4;
}
.goods .menu-wrapper .menu-item{
	padding: 16px 23px 15px 10px;
	border-bottom: 1px solid #E4E4E4;
	position: relative;
}
.goods .menu-wrapper .menu-item.current{
	background: white;
	font-weight: bold;
	margin-top: -1px;
}
.goods .menu-wrapper .menu-item .text{
	font-size: 13px;
	color: #333333;
	line-height: 17px;
	vertical-align: middle;
	/*多行顯示省略*/
	-webkit-line-clamp:2;
	display: -webkit-box;
	-webkit-box-orient:vertical;
	overflow: hidden;
}
.goods .menu-wrapper .menu-item .text .icon{
	width: 15px;
	height: 15px;
	vertical-align: middle;
}
.goods .menu-wrapper .menu-item .num{
	position: absolute;
	right: 5px;
	top: 5px;
	width: 13px;
	height: 13px;
	border-radius: 50%;
	color: white;
	background: red;
	text-align: center;
	font-size: 7px;
	line-height: 13px;
}
.goods .foods-wrapper{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
.goods .foods-wrapper .container-list{
	padding:11px 11px 0 11px;
	border-bottom: 1px solid #E4E4E4;
}
.goods .foods-wrapper .container-list img{
	width: 100%;
	margin-bottom: 11px;
	border-radius: 5px;
}
.goods .foods-wrapper .food-list{
	padding:11px;
}
.goods .foods-wrapper .food-list .title{
	height: 13px;
	font-size: 13px;
	background: url(data:image/png;base64,) no-repeat left center;
	background-size: 2px 10px;
	padding-left: 7px;
	margin-bottom: 12px;
}
.goods .foods-wrapper .food-list .food-item{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 25px;
	position: relative;
}
.goods .foods-wrapper .food-list .food-item .icon{
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 63px;
	        flex: 0 0 63px;
	background-position: center;
	background-size: 120% 100%;
	background-repeat: no-repeat;
	margin-right: 11px;
	height: 75px;
}
.goods .foods-wrapper .food-list .food-item .content{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
.goods .foods-wrapper .food-list .food-item .content .name{
	font-size: 16px;
	line-height: 21px;
	color: #333333;
	margin-bottom: 10px;
	padding-right: 27px;
}
.goods .foods-wrapper .food-list .food-item .content .desc{
	font-size: 10px;
	line-height: 19px;
	color: #bfbfbf;
	margin-bottom: 8px;
	/*超出*/
	-webkit-line-clamp:1;
	display: -webkit-box;
	-webkit-box-orient:vertical;
	overflow: hidden;
}
.goods .foods-wrapper .food-list .food-item .content .extra{
	font-size: 12px;
	color: #bfbfbf;
	margin-bottom: 7px;
}
.goods .foods-wrapper .food-list .food-item .content .saled{
	margin-right: 14px;
}
.goods .foods-wrapper .food-list .food-item .content .product{
	height: 15px;
	margin-bottom: 6px;
}
.goods .foods-wrapper .food-list .food-item .content .price{
	font-size: 0;
}
.goods .foods-wrapper .food-list .food-item .content .price .text{
	font-size: 14px;
	color: #fb4e44;
}
.goods .foods-wrapper .food-list .food-item .content .price .unit{
	font-size: 12px;
	color: #bfbfbf;
}
.goods .foods-wrapper .food-list .food-item .cartcontrol-wrapper{
	position: absolute;
	right: 0;
	bottom: 0;
}

.shopcart{
	width: 100%;
	height: 51px;
	background:#514f4f;
	position: fixed;
	left: 0;
	bottom: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.shopcart.highlight{
	background: #2d2b2a;
}
.shopcart .content-left{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
.shopcart .content-left .logo-wrapper{
	width: 50px;
	height: 50px;
	background: #666666;
	border-radius: 50%;
	position: relative;
	top: -14px;
	left: 10px;
	text-align: center;
	float: left;
}
.shopcart .content-left .logo-wrapper.highlight{
	background: #ffd161;
}
.shopcart .content-left .logo-wrapper .logo{
	font-size: 28px;
	color: #c4c4c4;
	line-height: 50px;
}
.shopcart .content-left .logo-wrapper .logo.highlight{
	color: #2d2b2a;
}
.shopcart .content-left .logo-wrapper .num{
	width: 15px;
	height: 15px;
	line-height: 15px;
	border-radius: 50%;
	font-size: 9px;
	color: white;
	background: red;
	position: absolute;
	right:0;
	top: 0;
}
.shopcart .content-left .desc-wrapper{
	float: left;
	margin-left: 13px;
}
.shopcart .content-left .desc-wrapper .total-price{
	font-size: 18px;
	line-height: 33px;
	color: white;
}
.shopcart .content-left .desc-wrapper .tip{
	font-size: 12px;
	color: #bab9b9;
	line-height: 51px;
}
.shopcart .content-left .desc-wrapper .tip.highlight{
	line-height: 12px;
}
.shopcart .content-right{
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 110px;
	        flex: 0 0 110px;
	font-size: 15px;
	color: #bab9b9;
	line-height: 51px;
	text-align: center;
	font-weight: bold;
}
.shopcart .content-right.highlight{
	color: #2d2b2a;
	background: #ffd161;
}

.cartcontrol{
	font-size: 0;
}
.cartcontrol .cart-decrease{
	display: inline-block;
	width: 26px;
	height: 26px;
	font-size: 26px;
	color: #b4b4b4;
}
.cartcontrol .cart-count{
	display: inline-block;
	width: 25px;
	text-align: center;
	font-size: 12px;
	line-height: 26px;
	vertical-align: top;
}
.cartcontrol .cart-add{
	display: inline-block;
	width: 26px;
	height: 26px;
	font-size: 26px;
	color: #ffd161;
	position: relative;
}
.cartcontrol .cart-add .bg{
	width: 20px;
	height: 20px;
	border-radius: 50%;
	background: black;
	position: absolute;
	left: 3px;
	top: 3px;
	z-index: -1;
}
.move-enter-active,.move-leave-active{
	-webkit-transition:all 0.5s linear;
	transition:all 0.5s linear;
}
.move-enter,.move-leave-to{
	-webkit-transform: translateX(20px) rotate(180deg);
	        transform: translateX(20px) rotate(180deg);
}
























